<template>
  <li class="cart__item product">
    <div class="product__pic">
      <img
        :src="item.product.image.file.url"
        width="120"
        height="120"
        :alt="item.product.title"
      />
    </div>
    <h3 class="product__title">
      {{ item.product.title }}
    </h3>
    <span class="product__code"> Артикул: {{ item.product.id }} </span>

    <CountProduct v-model="amount" :class="'product__counter form__counter'" />

    <b class="product__price">
      {{ (item.quantity * item.product.price) | numberFormat }} ₽
    </b>

    <button
      class="product__del button-del"
      type="button"
      aria-label="Удалить товар из корзины"
      @click.prevent="deleteCartProduct(item.product.id)"
    >
      <svg width="20" height="20" fill="currentColor">
        <use xlink:href="#icon-close"></use>
      </svg>
    </button>
  </li>
</template>

<script>
import numberFormat from "@/helpers/numberFormat";

import CountProduct from "./CountProductPage.vue";
import { mapActions } from "vuex";

export default {
  props: ["item"],
  components: { CountProduct },
  filters: {
    numberFormat,
  },
  computed: {
    amount: {
      get() {
        return this.item.quantity;
      },
      set(value) {
        this.$store.dispatch("updateCartProductAmount", {
          productId: this.item.product.id,
          amount: value,
        });
      },
    },
  },
  methods: {
    ...mapActions(["deleteCartProduct"]),
  },
};
</script>
