<template>
  <main class="content container">
    <div class="content__top">
      <ul class="breadcrumbs">
        <li class="breadcrumbs__item">
          <router-link to="/" class="breadcrumbs__link" href="index.html">
            Каталог
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <router-link :to="{ name: 'cart' }" class="breadcrumbs__link" href="cart.html">
            Корзина
          </router-link>
        </li>
        <li class="breadcrumbs__item">
          <a class="breadcrumbs__link"> Оформление заказа </a>
        </li>
      </ul>

      <h1 class="content__title">Корзина</h1>
      <span class="content__info"> {{ totalItem }} товара </span>
    </div>

    <section class="cart">
      
      <form class="cart__form form" @submit.prevent="order" >
        <BasePreloader v-if="orderLoading"/>
        <div class="cart__field" v-if="!orderLoading">
          <div class="cart__data">
            <BaseFormText
              v-model="formData.name"
              :error="formError.name"
              type="text"
              title="ФИО"
              placeholder="Введите ваше полное имя"
            />
            <BaseFormText
              v-model="formData.address"
              :error="formError.address"
              type="text"
              title="Адрес доставки"
              placeholder="Введите ваш адрес"
            />
            <BaseFormText
              v-model="formData.phone"
              :error="formError.phone"
              type="tel"
              title="Телефон"
              placeholder="Введите ваш телефон"
            />
            <BaseFormText
              v-model="formData.email"
              :error="formError.email"
              type="email"
              title="Email"
              placeholder="Введи ваш Email"
            />
            <BaseFormTextarea
              title="Комментарий к заказу"
              :error="formError.comment"
              v-model="formData.comment"
              placeholder="Ваши пожелания"
            />
          </div>
          
          <div class="cart__options">
            <h3 class="cart__title">Доставка</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="0"
                    checked=""
                  />
                  <span class="options__value"> Самовывоз <b>бесплатно</b> </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="delivery"
                    value="500"
                  />
                  <span class="options__value"> Курьером <b>500 ₽</b> </span>
                </label>
              </li>
            </ul>

            <h3 class="cart__title">Оплата</h3>
            <ul class="cart__options options">
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="card"
                  />
                  <span class="options__value"> Картой при получении </span>
                </label>
              </li>
              <li class="options__item">
                <label class="options__label">
                  <input
                    class="options__radio sr-only"
                    type="radio"
                    name="pay"
                    value="cash"
                  />
                  <span class="options__value"> Наличными при получении </span>
                </label>
              </li>
            </ul>
          </div>
        </div>

        <OrderCartInfo
          :products="products"
          :total-item="totalItem"
          :total-price="totalPrice"
        >
          <button class="cart__button button button--primery" type="submit">
            Оформить заказ
          </button>
        </OrderCartInfo>

        <div class="cart__error form__error-block" v-if="formErrorMessage.length">
          <h4>Заявка не отправлена!</h4>
          <p>
            {{ formErrorMessage }}
          </p>
        </div>
      </form>
    </section>
  </main>
</template>

<script>
import BaseFormText from "@/components/BaseFormText.vue";
import BaseFormTextarea from "@/components/BaseFormTextarea.vue";
import axios from "axios";
import OrderCartInfo from "@/components/OrderCartInfo.vue";
import { API_BASE_URL } from "@/config";
import { mapGetters } from "vuex";
import BasePreloader from "@/components/BasePreloader.vue";

export default {
  components: { BaseFormText, BaseFormTextarea, OrderCartInfo,BasePreloader },
  data() {
    return {
      formData: {},
      formError: {},
      formErrorMessage: {},

      orderLoading: false
    };
  },
  methods: {
    order() {
      this.formError = {};
      this.formErrorMessage = {};
      this.orderLoading = true
      new Promise((resolte) => setTimeout(resolte, 4000)).then(() => {
        axios
          .post(
            API_BASE_URL + "/api/orders",
            {
              ...this.formData,
            },
            {
              params: {
                userAccessKey: this.$store.state.userAccessKey,
              },
            }
          )
          .then((response) => {
            this.$store.commit("resetCart");
            this.$store.commit("updateOrderInfo", response.data);
            this.$router.push({ name: "orderInfo", params: { id: response.data.id } });
            this.orderLoading = false
          })
          .catch((error) => {
            this.formError = error.response.data.error.request || {};
            this.formErrorMessage = error.response.data.error.message;
            this.orderLoading = false
          });
      });
    },
  },
  computed: {
    ...mapGetters({
      products: "cartDetailProducts",
      totalPrice: "cartTotalPrice",
      totalItem: "cartTotalItem",
    }),
  },
};
</script>
